import { Component, OnInit } from '@angular/core';
import {IProducts} from './products'
import { MyserviceService } from './../myservice.service';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {

pageTitle= 'Product List'
products: IProducts[];
bShowImage = false;
imageWidth = 50;
listFilter ;
bListFilter;
dolistFilter;

  constructor(private service: MyserviceService) { }

  ngOnInit() {
    // console.log(this.products); // JSON.stringify(this.products));
    this.service.getProducts().subscribe(products_ => this.products = products_ );
  }

  ShowImage() {
    this.bShowImage = !this.bShowImage;
  }

  onRaitingClicked( s: string) {
    console.log(s);
  }

  onKeypress($event) {
    if (this.bListFilter === this.listFilter) {
      console.log('Enter');
      this.dolistFilter = this.bListFilter;
    } else {
      this.bListFilter = this.listFilter;
    }
  }
}



