import { Component, OnInit } from '@angular/core';
import { IProducts } from './../product-list/products'
import { ActivatedRoute , Router } from '@angular/router'
import { MyserviceService } from './../myservice.service';

@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.css']
})
export class ProductDetailComponent implements OnInit {
  id: string;
  products: IProducts[];
  
  constructor( private _router: ActivatedRoute, private _r: Router , private service: MyserviceService) { }

  ngOnInit() {
    this.id = this._router.snapshot.params['id'];
    this.service.getProducts().subscribe(products_ => this.products = products_ );
  }

  btnClick(): void {
    this._r.navigate(['/products']);
  }

}



