import { Pipe, PipeTransform } from '@angular/core';
import {IProducts} from './product-list/products';

@Pipe({
  name: 'filterPipe'
})
export class FilterPipePipe implements PipeTransform {

  transform(value: IProducts[], args?: any): IProducts[] {
    return args ? value.filter(
      (p1: IProducts) => p1.productName.toLocaleLowerCase().indexOf(args.toLocaleLowerCase()) !== -1
    ) : value;
  }

}



